:root{
  --padding: 50px;
  --main-background: rgb(233, 220, 220);
  --secondary-font: 'Klee One', cursive;
  --bage-color: rgb(247, 236, 178);
}

*{
  box-sizing: border-box;
}

a{
  text-decoration: none;
  color: black;
}

a:hover{
  text-decoration: none;
  color: black;
}

ul{
  list-style: none;
  padding: 0;
  margin: 0;
}

html{
  scroll-behavior: smooth;
}

body{
  font-family:'Courier New', Courier, monospace;
}

#title{
  display: block;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 50px;
  width: fit-content;
  background-color: var(--bage-color);
  padding: 0 15px;
  margin: -20px 20px 20px;
  transform: skewX(-10deg) rotate(-3deg);
}

#logo{
  display: block;
  font-size: 50px; 
  text-align: center;
}

.nav-links{
  display: flex;
}

nav{
  background-color: var(--main-background);
  display: flex;
  align-items: center;
  padding-right: 20px;
  padding-left: 20px;
  justify-content: space-between;
}

nav a {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  padding: 20px;
  font-family: var(--secondary-font);
}

nav a:hover{
  background-color: var(--bage-color);
}

nav input[type='search']{
  outline: none;
  border: 1px solid #ccc;
  padding: 4px ;
  width: 300px;
  border-radius: 6px;
}

main{
  padding-top: var(--padding) ;
  padding-bottom: var(--padding);
  font-family: var(--secondary-font);
  flex-grow: 2;
}

.articles-container{
  background-color: #cccccc7a;
  padding: 5px;
  border-radius: 6px;
  margin-bottom: 20px;
}

.article{
  line-height: 1.6;
  background-color: #f8f9fa;
  padding: 30px;
  border-radius: inherit;
}

.articles-container h2{
  margin-bottom: 10px;
  width: fit-content;
  color: #222;
  padding: 5px 10px;
}

.articles-container p{
  font-weight: 800;
}

.article .more a{
  display: block;
  font-size: 20px;
  color: white;
}

.aside{
  background-color: hsl(210, 17%, 98%);
  height: fit-content;
  padding: 15px;
  border-radius: 6px;
  margin-top: 20px;
  text-align: -webkit-center;
}

.aside h3{
  width: max-content;
}

aside ul a{
  display: block;
  padding: 8px 0;
  width: inherit
}

aside ul a:hover{
  background-color: var(--bage-color);
}

.homeImg{
  text-align: center;
}

.homeImg img{
  width: calc(100% / 4);
  height: 100px;
}

.articleImg{
  text-align: center;
  margin-bottom: 15px;
}

.articleImg img{
  width: calc(100% / 2);
}

.write-title{
  text-align: center;
  margin-bottom: 20px;
}

.postArticle{
  font-family: var(--secondary-font);
}

.postArticle input[type='text']{
  display: block;
  margin: 10px 5px;
  padding: 10px;
  width: 40%;
  border: 1px solid #ccc;
  border-radius: 6px;
  outline: none;
}

.postArticle textarea{
  display: block;
  width: 100%;
  height: 200px;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 6px;
  outline: none;
} 

.postArticle .buttons{
  margin-top: 20px;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.routers-links{
  display: flex;
  padding: 15px;
}

.active{
  background-color: var(--bage-color);
}

/* Media */
@media (max-width:991px) {
  #logo{
    font-size: 32px;
  }
  .routers-links{
    flex-direction: column-reverse;
  }
  nav{
    flex-direction: column;
  }
  nav a{
    font-size: 16px;
    padding: 10px;
  }
  nav .search{
    display: none;
  }
}